export function validateRegisterInfo(values) {
	let errors = {};
	// console.log("Validating", values);
	if (!values?.username?.trim()) {
		errors.username = "Username is required";
	}

	if (values?.email) {
		if (!/\S+@\S+\.\S+/.test(values.email)) {
			errors.email = "Email address is invalid";
		}
	}
	if (!values?.phone_number) {
		errors.phone_number = "Phone Number is required";
	} else if (
		values?.phone_number <= 99361000000 ||
		values?.phone_number >= 99365999999
	) {
		errors.phone_number =
			"Phone Number must be in range of 99361000000 and 99365999999";
	}

	if (!values?.password) {
		errors.password = "Password is required";
	}
	if (values?.password !== values?.re_password) {
		errors.re_password = "Confirm password must be same as password";
	}
	if (!values?.re_password) {
		errors.re_password = "Confirm password is required";
	}

	return errors;
}
