import React, { useEffect, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { signup } from "../redux/actions/auth";
import { useDispatch, useSelector } from "react-redux";
import { useTranslation } from "react-i18next";
import {
	validateInfo,
	validateRegisterInfo,
} from "../components/Validate/validateOrder";
import isEmpty from "../components/Validate/isEmpty";

const mapState = ({ auth }) => ({
	isAuthenticated: auth.isAuthenticated,
});

function Signup() {
	const { t } = useTranslation(["account"]);
	const [formData, setFormData] = useState({
		username: "",
		phone_number: "993",
		email: "",
		password: "",
		re_password: "",
	});

	const { username, phone_number, email, password, re_password } = formData;
	const dispatch = useDispatch();
	const [isLoading, setIsLoading] = useState(false);
	const [formErrors, setFormErrors] = useState(null);
	const { isAuthenticated } = useSelector(mapState);
	const [startedWriting, setStartedWriting] = useState(null);
	const onChange = (e) => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};
	useEffect(() => {
		setFormErrors(validateRegisterInfo(formData));
	}, [formData, startedWriting]);

	const onSubmit = (e) => {
		e.preventDefault();
		setFormErrors(validateInfo(formData));
		setStartedWriting(true);
		if (isEmpty(formErrors)) {
			setIsLoading(true);
			dispatch(
				signup(username, phone_number, email, password, re_password)
			).then((res) => {
				setIsLoading(false);
			});
		}
	};

	if (isAuthenticated) return <Redirect to="/" />;

	return (
		<div
			className="  bg-white pt-4 p-5 mx-4 mx-sm-auto"
			style={{
				borderRadius: "30px",
				maxWidth: "400px",
				margin: "70px auto",
				boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
			}}
		>
			<h2 style={{ fontWeight: 700 }} className="text-center py-3 pb-4">
				{t("account:Create an Account")}
			</h2>
			<form onSubmit={(e) => onSubmit(e)}>
				<div className=" my-3">
					<input
						type="text"
						className={`form-control ${
							startedWriting &&
							(formErrors?.username ? "is-invalid" : "is-valid")
						}`}
						placeholder={t("account:Username")}
						name="username"
						value={username}
						style={{ borderRadius: "15px" }}
						onChange={(e) => onChange(e)}
						required
					/>
					<div class="invalid-feedback">{formErrors?.username}</div>
				</div>
				<div className="my-3">
					<input
						type="email"
						class={`form-control ${
							startedWriting && (formErrors?.email ? "is-invalid" : "is-valid")
						}`}
						name="email"
						value={email}
						onChange={(e) => onChange(e)}
						placeholder="Email"
						style={{ borderRadius: "20px" }}
					/>

					<div class="invalid-feedback">{formErrors?.email}</div>
				</div>
				<div className="my-3">
					<input
						type="number"
						className={`form-control ${
							startedWriting &&
							(formErrors?.phone_number ? "is-invalid" : "is-valid")
						}`}
						placeholder={t("account:Phone Number")}
						name="phone_number"
						value={phone_number}
						style={{ borderRadius: "15px" }}
						onChange={(e) => onChange(e)}
						required
					/>
					<div class="invalid-feedback">{formErrors?.phone_number}</div>
				</div>
				<div className="my-3">
					<input
						type="password"
						className={`form-control ${
							startedWriting &&
							(formErrors?.password ? "is-invalid" : "is-valid")
						}`}
						placeholder={t("account:Password")}
						name="password"
						value={password}
						style={{ borderRadius: "15px" }}
						onChange={(e) => onChange(e)}
						required
					/>
					<div class="invalid-feedback">{formErrors?.password}</div>
				</div>
				<div className="my-3">
					<input
						type="password"
						className={`form-control ${
							startedWriting &&
							(formErrors?.re_password ? "is-invalid" : "is-valid")
						}`}
						placeholder={t("account:Confirm password")}
						name="re_password"
						value={re_password}
						style={{ borderRadius: "15px" }}
						onChange={(e) => onChange(e)}
						required
					/>
					<div class="invalid-feedback">{formErrors?.re_password}</div>
				</div>
				<div className="row">
					<button
						className="btn btn-primary mx-auto my-4 "
						style={{
							maxWidth: "300px",
							borderRadius: "15px",
							fontSize: "18px",
						}}
						type="submit"
					>
						{isLoading ? (
							<div class="spinner-border text-white" role="status">
								<span class="visually-hidden">Loading...</span>
							</div>
						) : (
							t("account:Sign up")
						)}
					</button>
				</div>
			</form>
			<p className="mt-4">
				{t("account:Already have an account?")}{" "}
				<Link
					to="/login"
					style={{ fontSize: "15px", borderRadius: "10px" }}
					className="badge  btn-primary my-1 ms-3 py-2  text-decoration-none"
				>
					{t("account:Login")}{" "}
					<i class="bi bi-arrow-right-circle-fill ms-2 "></i>
				</Link>
			</p>
		</div>
	);
}

export default Signup;
